/* tabs */
.tabboxs{background: var(--gray-light);}
.tabboxs li{
  position: relative;
  padding: 10px 15px;
  display: inline-block;
  background: #fff;
  border: 1px solid #dcd9d9;
  margin-right: 1px;
  max-width: 170px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  cursor: pointer;
}
.tabboxs li.active { border-bottom: 0; background: var(--primary); color:#fff }
.tabboxs li span{overflow: hidden; text-overflow: ellipsis; white-space: nowrap;}
.tabboxs li i{display: none; position: absolute; right: 4px; cursor: pointer; font-size: 18px; top: 11px; width: 23px; text-align: right;}
.tabboxs li:hover i{display: block; background: #fff}
.tabboxs li.active:hover i{background: var(--primary)}
.tabboxs .tabs{ overflow: hidden; overflow-x: auto}
.tabboxs .tabs::-webkit-scrollbar { width: 0 !important; display: none; }
.tabboxs .tabs{ overflow: -moz-scrollbars-none;  -ms-overflow-style: none; }
.tabboxs .addtabs i{margin-right: 0; margin-left: 1px; min-width: auto; padding: 11px; cursor: pointer;}
.leftside{ min-height: calc(100vh - 230px); }